    background-position: left 100% top 50%;
    background-size: 0.4em auto, 100%;
}

select:focus,
input:focus {
    outline: none;
}

input {
    background-color: transparent;
    cursor: pointer;
    outline: none;
}


/* ==================== Website structure ==================== */

section {
    width: 95%;
    margin: auto auto;
    padding: 15vh 0;
    background-color: rgb(212, 198, 198);
}

footer {
    margin: 1vh auto;
    width: 95%;
}

header {
    padding: 0;
}

aside {
    width: 25%;
    margin: 4vh 0.5vw 0 0;
}


/* ==================== Lists ==================== */

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

li:hover {
    color: #6a983c;
}


/* ==================== Buttons ==================== */

button {
    display: flex;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    border: none;
    font-size: 1.1em;
    line-height: 1.2em;
    border-radius: 12px;
    cursor: pointer;
    align-items: center;
}

button:hover {
    box-shadow: 1px -1px 10px 2px rgba(34, 80, 43, 0.2) inset;
}


/* ==================== Other elements ==================== */

p {
    margin: 0;
    padding: 1vh 0;
}

a {
    color: inherit;
    text-decoration: none;
}

hr {
    width: 90%;
    padding: 0 1.5vw;
    border-bottom: 0.1vh solid rgba(245, 234, 234, 0.678);
    border-top: none;
    border-left: none;
    border-right: none;
    margin: 0 auto;
}


